	module.exports = {"data":{"markdownRemark":{"html":"<p>CCTV「東の息子」は教師として、学生を教えることは楽しいことだと言いました。彼が言ったのは「一種の人間交換」だった。一般的に良い教師はこの気持ちを持っています学生は、生き生きした残響、生きている知識オブジェクト、体の意識のプロセス、双方向の情報交換を提供します。これは他の職業ではおそらく不可能です。これは先生の最大の喜びです。しかし、このような幸せな人生を体験するためには、最初にやるべきことは、苦しみ、つまり欲望の抑制です。これは、今日の世界では、繁栄と物質主義の機会が特に重要です。</p>\n<p>明らかにするのが難しい論理があります。人が先生になると、このような楽しみを経験するのは難しいでしょう。知識、人格、人生で経験できないため、上記の痛みを経験する方がより多くです。様々な力によって、彼は教師のポジションを離れたり、教師の真の霊的な領域を失います。このようにして、彼は教師の真の幸福、人生とのコミュニケーションの喜び、人生の幸福を決して経験しません。そして、材料の面で満足しています。欲求を抑圧する苦痛の後でさえ、才能はこの高いレベルの楽しみを楽しむことができます。問題は、このような楽しみを何人も楽しむことができるかということです。</p>\n<p>人生、特に人生の成功の喜びを形作るよりも楽しいことはありません。人生を形作る最初の義務を負っている人は誰ですか？母親が教材に与えた人生に加えて、教師は精神的な生活を形成するために最も影響力があり、最も重要ではありません。</p>\n<p>生徒が育つのを見るのは霊的な喜びです。 「精神的な痛みはありますか？」実際には、「あなたは霊的な幸せを持っていますか？」と質問しています。ほとんどの人はこのレベルの苦しい幸福について話すことはできません。しかし、霊的な痛みや幸福を持たない人は、この世に来た人として存在することができますか？それは本当の人生を持っている人ですか？</p>\n<p>なぜですか？なぜ、創造的な幸福が必要ですか？</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0118.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0118.md","frontmatter":{"title":"6月26日 日曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0118.md absPath of file >>> MarkdownRemark"}}

